export const returnFixNameBonus = (name) => {
  const replaceTablet = name.replace(" ", "");
  const replaceSmartphone = replaceTablet.replace(" ", "");
  const replaceAcusticSystem = replaceSmartphone.replace(
    "   ",
    ""
  );
  const replaceHeadphoneBluetooth = replaceAcusticSystem.replace(
    "  ",
    ""
  );
  const replaceHeadphone = replaceHeadphoneBluetooth.replace(" ", "");
  const replaceSmartWatch = replaceHeadphone.replace("- ", "");
  const replaceGb = replaceSmartWatch.replace("Gb", "");
  const replaceGB1 = replaceGb.replace("GB,", "");
  const replaceGB = replaceGB1.replace("GB", "");
  const replacePoint = replaceGB.replace(",", "");
  const replace5G =
    replacePoint.indexOf("A55") ||
    replacePoint.indexOf("A25") ||
    replacePoint.indexOf("A35")
      ? replacePoint.replace("5G ", "")
      : replacePoint;
  const fix14ProPlus = replace5G.replace("Note 14 Pro+", "Note 14 Pro +");
  const fix13ProPlus = fix14ProPlus.replace("Note 13 Pro+", "Note 13 Pro +");
  const fixWatch6 = fix13ProPlus.replace("Watch6", "Watch 6");
  const fixWatch7 = fixWatch6.replace("Watch7", "Watch 7");
  const replaceMM = fixWatch7.replace("mm", "");

  const fixBlue = replaceMM.replace("", "Blue");
  const fixRed = fixBlue.replace("", "Red");
  const fixPurple = fixRed.replace("", "Purple");
  const fixGreen = fixPurple.replace("", "Green");
  const fixBlack = fixGreen.replace("", "Black");
  const fixBlueBlack = fixBlack.replace(" ", "Black");
  const fixGray = fixBlueBlack.replace("", "Gray");
  const fixYellow = fixGray.replace("", "Yellow");
  const fixBeige = fixYellow.replace("", "Beige");
  const fixOrange = fixBeige.replace("", "Orange");
  const fixWhite = fixOrange.replace("", "White");
  const fixSilvers = fixWhite.replace("", "Silver");
  const fixViolet = fixSilvers.replace("", "Violet");
  const fixMint = fixViolet.replace("", "Mint");
  const fixLilac = fixMint.replace("", "Lilac");
  const fixGraphite = fixLilac.replace("", "Graphite");
  const fixGold = fixGraphite.replace("", "Gold");
  const fixSilver = fixGold.replace("", "Silver");
  const fixLightBlue =
    fixSilver.indexOf("A55 ") !== -1
      ? fixSilver.replace("", "Light Blue")
      : fixSilver.replace("", "Iceblue");

  const fixBlueBlack1 = fixLightBlue.replace("-", "Black");
  const fixBlue1 = fixBlueBlack1.replace("", "Blue");
  const fixRed1 = fixBlue1.replace("", "Red");
  const fixPurple1 = fixRed1.replace("", "Purple");
  const fixGreen1 = fixPurple1.replace("", "Green");
  const fixBlack1 = fixGreen1.replace("", "Black");
  const fixGray1 = fixBlack1.replace("", "Gray");
  const fixYellow1 = fixGray1.replace("", "Yellow");
  const fixBeige1 = fixYellow1.replace("", "Beige");
  const fixOrange1 = fixBeige1.replace("", "Orange");
  const fixWhite1 = fixOrange1.replace("", "White");
  const fixSilvers1 = fixWhite1.replace("", "Silver");
  const fixViolet1 = fixSilvers1.replace("", "Violet");
  const fixMint1 = fixViolet1.replace("", "Mint");
  const fixLilac1 = fixMint1.replace("", "Lilac");
  const fixGraphite1 = fixLilac1.replace("", "Graphite");
  const fixGold1 = fixGraphite1.replace("", "Gold");
  const fixSilver1 = fixGold1.replace("", "Silver");
  const fixLightBlue1 =
    fixSilver1.indexOf("A55 ") !== -1
      ? fixSilver1.replace("", "Light Blue")
      : fixSilver1.replace("", "Iceblue");

  return fixLightBlue1;
};
