export const returnFixNameRootOpt = (name) => {
  const replaceTablet = name.replace(" ", "");
  const replaceSmartphone = replaceTablet.replace(" ", "");
  const replaceAcusticSystem = replaceSmartphone.replace(
    "   ",
    ""
  );
  const replaceHeadphoneBluetooth = replaceAcusticSystem.replace(
    "  ",
    ""
  );
  const replaceHeadphone = replaceHeadphoneBluetooth.replace(" ", "");
  const replaceSmartWatch = replaceHeadphone.replace("- ", "");
  const replaceGb = replaceSmartWatch.replace("Gb", "");
  const replaceGB = replaceGb.replace("GB", "");
  const replaceGBRus = replaceGB.replace("", "");
  const replaceMM = replaceGBRus.replace("mm", "");
  const replaceMMRus = replaceMM.replace(" ", "");
  const replaceWatch6 = replaceMMRus.replace("Watch6", "Watch 6");
  const replaceWatch7 = replaceWatch6.replace("Watch7", "Watch 7");
  const replaceSimbol = replaceWatch7.replace(", ", "");
  const replaceSimbol1 = replaceSimbol.replace("8/256, Gold", "8/256 Gold");
  const replaceS24FE = replaceSimbol1.replace("S24FE", "s24 FE");
  const fix2a = replaceS24FE.replace("2", "2a");
  const fix3a = fix2a.replace("3", "3a");
  const fixZFlip7 = fix3a.replace("Z Flip7", "Z Flip 7");
  const fixZFold7 = fixZFlip7.replace("Z Fold7", "Z Fold 7");
  const fixce5 = fixZFold7.replace("CE 5", "ce5");
  const replaceS23FE = fixce5.replace("S23FE", "s23 FE");
  const replaceNFC = replaceS23FE.replace("NFC", "");
  const replaceBluetooth = replaceNFC.replace("  ", "");

  const fixBlue = replaceBluetooth.replace("", "Blue");
  const fixRed = fixBlue.replace("", "Red");
  const fixPurple = fixRed.replace("", "Purple");
  const fixGreen = fixPurple.replace("", "Green");
  const fixBlack = fixGreen.replace("", "Black");
  const fixBlueBlack = fixBlack.replace(" ", "Black");
  const fixGray = fixBlueBlack.replace("", "Gray");
  const fixYellow = fixGray.replace("", "Yellow");
  const fixBeige = fixYellow.replace("", "Beige");
  const fixOrange = fixBeige.replace("", "Orange");
  const fixWhite = fixOrange.replace("", "White");
  const fixSilvers = fixWhite.replace("", "Silver");
  const fixViolet = fixSilvers.replace("", "Violet");
  const fixMint = fixViolet.replace("", "Mint");
  const fixLilac = fixMint.replace("", "Lilac");
  const fixGraphite = fixLilac.replace("", "Graphite");
  const fixGold = fixGraphite.replace("", "Gold");
  const fixTb = fixGold.replace(" ", "Tb");
  const fixDot = fixTb.replace(",", "");
  const fixFold6 = fixDot.replace("Fold6", "Fold 6");
  const fixFlip6 = fixFold6.replace("Flip6", "Flip 6");
  const fixSilver = fixFlip6.replace("", "Silver");
  const fixS25Icyblue =
    fixSilver.indexOf("S25") !== -1
      ? fixSilver.replace("Ice Blue", "icyblue")
      : fixSilver;
  const fixLightBlue =
    fixS25Icyblue.indexOf("A55 ") !== -1
      ? fixS25Icyblue.replace("", "Light Blue")
      : fixS25Icyblue.replace("", "Iceblue");

  const fixS10 = fixLightBlue.replace("Series 10", "S10");
  const fixPoco5G =
    fixS10.indexOf("POCO") !== -1 || fixS10.indexOf("Galaxy") !== -1
      ? fixS10.replace("5G ", "")
      : fixS10;
  const fixPocoEU =
    fixPoco5G.indexOf("POCO") !== -1 ? fixPoco5G.replace("EU", "") : fixPoco5G;

  return fixPocoEU;
};
